#corbado-username:focus {
    outline: none;
}

#corbado-username:-webkit-autofill,
#corbado-username:-webkit-autofill:hover,
#corbado-username:-webkit-autofill:focus,
#corbado-username:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px var(--primary-background-color, '#ffffff') inset !important;
    border: 1px solid var(--border-color, rgba(143, 155, 191, 0.5));
    font-size: 1rem;
    -webkit-text-fill-color: var(--text-color, #535e80)!important;
    box-shadow: 0 0 0 1000px white inset;
}

#corbado-username {
    color: var(--text-color, #535e80);
    font-size: 1rem;
    padding: var(--input-padding, 0.8rem 1.4rem);
    width: 100%;
    border-width: 1px;
    border-style: solid;
    border-radius: var(--border-radius, 1.5rem);
    border-color: var(--border-color, rgba(143, 155, 191, 0.5));
    position: relative;
    background: transparent;
    z-index: 2;
    box-sizing: border-box;
    -webkit-text-fill-color: var(--text-color, #535e80);
    -webkit-background-clip: text !important;
    background-clip: text !important;
}
